/* jshint ignore:start */

define('coffeecodephilly/config/environment', ['ember'], function(Ember) {
  var prefix = 'coffeecodephilly';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("coffeecodephilly/tests/test-helper");
} else {
  require("coffeecodephilly/app")["default"].create({"name":"coffeecodephilly","version":"0.0.1+1401736d"});
}

/* jshint ignore:end */
