define('coffeecodephilly/router', ['exports', 'ember', 'coffeecodephilly/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route('coffee', function () {
      this.route('index', { path: '/' });
      this.route('new');
      this.route('show', { path: ':coffee_id' });
      this.route('map');
    });
    this.route('login');
    this.route('admin');
    this.route('cclist');
    this.route('about');
  });

  exports['default'] = Router;

});