define('coffeecodephilly/routes/admin', ['exports', 'ember', 'simple-auth/mixins/authenticated-route-mixin'], function (exports, Ember, AuthenticatedRouteMixin) {

	'use strict';

	exports['default'] = Ember['default'].Route.extend(AuthenticatedRouteMixin['default'], {
		model: function model(params) {
			return this.store.findAll('coffee');
		},

		setupController: function setupController(controller, coffees) {
			controller.set('coffees', this.store.findAll('coffee'));
		},

		actions: {
			'delete': function _delete(coffee) {
				coffee.destroyRecord();
			}
		}
	});

});