define('coffeecodephilly/controllers/coffee/new', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Controller.extend({
		isValid: Ember['default'].computed('model.name', 'model.address', function () {
			return !Ember['default'].isEmpty(this.get('model.name')) && !Ember['default'].isEmpty(this.get('model.address'));
		}),

		actions: {
			save: function save() {
				if (this.get('isValid')) {
					var _this = this;
					this.get('model').save().then(function (coffee) {
						_this.transitionToRoute('coffee.show', coffee);
					});
				} else {
					this.set('errorMessage', 'You have to fill in all the inputs');
				}

				return false;
			},

			cancel: function cancel() {
				this.transitionToRoute('coffee');
				return false;
			}
		}
	});

});